:root{
  --Nav-font-size: 1.2em
}

*:focus{
  outline: 1px solid rgb(223, 52, 0);
}
*{
  color:white;
  margin: 0;
  padding: 0;
  
}
body{
  background-color: #222222;
  -khtml-user-select: none;
    -o-user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}
#root{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

#content img{
  border-radius: 1vw 0;
}
#content{
  width: 95vw;
  margin: auto;
  margin-top: 5vw;
}
#Header{
  background-color: #222222;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0 2vw;
  font-size: var(--Nav-font-size);
  box-sizing: border-box;
  width:100%;
  min-height: 55px;
  height: 5vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
}
#HeaderLeft{
  display: flex;
}
#HeaderRight{
  display: flex;
}
#NavHeader-ul{
  list-style: none;
  display: flex;
}
#NavHeader-ul li{margin-right: 10px;}
#NavHeader-ul a{
  padding: 5px 12px;
  text-decoration: none;
  display: block;
}
#NavHeader-ul a:hover{
  background-color: #1a1a1a;
}
#Logo{
  min-width: 40px;
  min-height: 40px;
  width: 3.1vw;
  height: 3.1vw;
}
#divImg{
  min-width: 40px;
  min-height: 40px;
  width: 3.1vw;
  height: 3.1vw;
  background-color: white;
  border-radius: 100%;
  margin-right: 10px;
}

#Search{
  background-color: #1a1a1a;
  border-radius: 5px;
  position: relative;
  display: block;
  padding: 5px 12px;
  text-decoration: none;
  padding-left: 36px;
}
#Search::after{
  position: absolute;
  top: 4px;
  left: 6px;
  content: " ";
  display: block;
  background: url("./Orion_search-magnify.svg");
  width: 30px;
  height: 30px;
  background-size: 30px 30px;
}
#divAvatar{
  position: relative;
}
#Avatar{
  background: none;
  border: none;
}
#Avatar img{
  min-height: 35px;
  min-width: 35px;
  width: 2.65vw;
  height: 2.65vw;
}

#AvatarBar{
  z-index: 99;
  font-size: .9em;
  position: absolute;
  top: 120%;
  right: 0;
  background: #1a1a1a;
  padding: 10px;
}
#AvatarBar a, #Login {
  border-radius: 5px;
  padding: 5px 12px;
  text-decoration: none;
  display: block;
}
#AvatarBar a:hover{background-color: #141414;}
#Login{ background-color: #ce5200; }
#Login:hover{ background-color: #b64900; }


footer{
  position: absolute;
  left: 0;
  margin-top: 4.5vw;
  width: 100%;
  height: 9vw;
  min-height: 150px;
  background-color: #141414;
  z-index: -2;
}





#Error {
  width: 94vw;
  text-align: center;
  height: auto;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
#Error p{
  font-size: 1.3em;
}
#Error h1{
  font-size: 3em;
}





#MainSlider{
  overflow: hidden;
  margin: 1vw 0 1vw 0;
  width: 100%;
  user-select: none;
  min-height: 220px;
  height: 26vw;

}

#MainSliderContainer{
  min-height: 190px;
  height: 24.76vw;
  transition: .5s;
  position: relative;
  display: flex;
  width: 100%;
}
.MainSliderChild{
  box-sizing: border-box;
  background-color: #181818;
  min-width: 100%;
  padding: 1%;
}
.MainSliderChild a{
  outline: none;
  text-decoration: none;
  height: 100%;
  cursor: default;
  display: flex;
  align-items: center;
}

.MainSliderChild img{
  background-color: #141414;
  width: 21vw;
  height: 100%;
}
.MainSliderChild>a>div{
  overflow: hidden;
