#CommentsText{
  display: block;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 64 64' aria-labelledby='title' aria-describedby='desc' role='img' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Ctitle%3EComments%3C/title%3E%3Cdesc%3EA solid styled icon from Orion Icon Library.%3C/desc%3E%3Cpath data-name='layer2' fill='%23707070' d='M52 4.5H2v30h8v11l11.1-11H52v-30z'%3E%3C/path%3E%3Cpath data-name='layer1' fill='%23707070' d='M56 26.5v12H28v10h16.9l11.1 11v-11h6v-22h-6z'%3E%3C/path%3E%3C/svg%3E");
}
#settingsForText>div>button{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4vw;
  width: 4vw;
  min-height: 48px;
  min-width: 48px;
  border: none;
  background: transparent;
}
#settingsForText>div>button:hover{
  background-color: #141414;
}
.SettingsWindow{
  position: fixed;
  top: 5vw;
  right: 4vw;
  width: 30vw;
  min-width: 300px;
  height: 100%;
  background: #191919;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
}
#settingsForText>div>button:focus{
  background: #141414;
  outline: none;
}
#SettChoose{
  width: 90%;
}
#SettChoose a{
  color: #d1d1d1;
  margin:1vw 0 ;
  width: 100%;
  display: block;
  padding: .4vw;
  text-decoration: none;
}

#SettChoose a:hover{
  background: #131313;
}
#nameChapter, #nameTitle{
  font-weight: 500;
  margin: 2vw;
  text-align: center;
}
#nameChapter + hr{
  margin-bottom: 1.4vw;
}
#fontSizeSetting,#lineSetting{
  margin: .5vw 0;
  display: block;
  width: 100%;
}
#fontSizeSettingLabel,#lineSettingLabel{
  color: #d1d1d1;
  display: block;
  width: 100%;
  text-align: left;
  font-size: 1.2em;
  margin-top: 2vw;
}
#SettFont{
  margin-top: 1vw;
  display: flex;
  width: inherit;
  justify-content: space-between;
}
#SettFont > div{
  border-radius: 1vw;
  width: 43%;
  padding: .6vw;
  background: #272727;
  border: 1px solid rgb(46, 46, 46);
}
#SettFontTahoma{
  font-family: Tahoma, Geneva, Verdana, sans-serif;
}
#SettFontGeorgia{
  font-family: Georgia, 'Times New Roman', Times, serif;
}

#SettingComments{
  width: 95%;
  height: 80%;
}
#SettingComments>div:first-child{
  position: relative;
  height: 2.2vw;
  min-height: 36px;
}
#SettingComments >div:last-child{
  overflow: auto;
  height: 80%;
  padding: .5vw;
  box-sizing: border-box;
}
#CommentsChapter{
  box-sizing: border-box;
  margin-bottom: 1.5vw;
  margin: 0;
  height: 2.2vw;
  min-height: 36px;
  color: #191919;
  resize: none;
  width: 100%;
  border: none;
  padding: 0 2.8vw 0 1vw;
  border-radius: 1vw;
}
.CommsAnswerChapter{
  margin-top: 1.5vw;
  display: flex;
  width: inherit;
  text-align: left;
}
.CommsAnswerChapter>div{
  display: flex;
  flex-direction: column;
}
.CommsAnswerChapter>div>p{
  word-wrap: break-word;
  margin: 1vw 0;
  width: inherit;
  word-break: break-all;
  line-height: 1.8em;
}
#content .CommsAnswerChapter>img{
  flex: 0 0 auto;
  min-width: 27px;
  min-height: 27px;
  width: 2.2vw;
  height: 2.2vw;
  background-color: #131313;
  border-radius: 100%;
  margin-right: 1vw;
}
.CommsAnswerChapter button{
  border: none;
  background: #131313;
  padding: .5vw;
  font-size: 1.1em;
}
.CommsAnswerChapter button:hover{
  background: #111111;
}

#SettingCommentsButtonContainer{
  border: none;
  border-radius: .3vw;
  background: #d86800ce;
  position: absolute;
  right: 1vw;
  bottom: 50%;
  transform: translateY(50%);
  width: 24px;
  height: 24px;
  font-size: 1.2em;
  color: #1b1b1b;
}

.titleFooter{
  display: flex;
  align-items: center;
  width: 90%;
  margin: 1vw auto;
  justify-content: space-between;
}
.titleFooter div:first-child{
  text-align: center;
}
.titleFooter div:first-child img{
  width: 2.5vw;
  height: 2.5vw;
  min-width: 35px;
  min-height: 35px;
  background: #141414;
}
.titleFooter div:last-child button{
  background: transparent;
  min-width: 80px;
  min-height: 25px;
  width: 5.9vw;
  height: 2vw;
  border-radius: 1vw;
  border: 1px solid #da6900;
}

.titleNext{
  position: relative;
  height: 1vw;
  min-height: 25px;
}
.titleNext a{
  display: block;
  text-decoration: none;
  background: #161616;
  padding: .4vw 1vw;
}
.titleNext a:hover{
  background: #101010;
}
#prev{
  position: absolute;
  left: 8%;
}
#next{
  position: absolute;
  right: 8%;
}



#menuCategory{
  float: right;
}
.inputMenuText{
  padding: 0 1vw;
  margin: 1vw 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #141414;
  border: 1px solid #ce5200;
  border-radius: .5vw;
  min-height: 40px;
  width: 18vw;
  position: relative;
}
.inputMenuText >div:first-child,.inputMenuText input{
  width: 100%;
}
.inputMenuText>div:nth-child(2) button{
  border: none;
  background: transparent;
  font-size: 1.2em;
  transform: rotate(-90deg);
  width: 1.8vw;
  min-width: 35px;
  height: 100%;
  display: flex;
  align-self: center;
  justify-content: space-around;
}
.inputMenuText>div:nth-child(2):hover{
  background: #101010;
}
#menuResult{
  width: 76%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
}
#menuCategory>div>div input{
  background: transparent;
  border: none;
  outline:  none;
}
.MenuList{
  z-index: 4;
  background: #141414;
  display: block;
  top: 100%;
  left: 0;
  position: absolute;
  width: 100%;
  max-height: 120px;
  min-height: 50px;
  overflow: auto;
  border-left: 1px solid #ce5200;
  border-bottom: 1px solid #ce5200;
  border-right: 1px solid #ce5200;
}
.MenuList button{
  width: 100%;
  padding: .5vw;
  font-size: 1em;
  display: block;
  background: transparent;
  border: none;
}
.MenuList button:hover{
  background: #111111;
}
.MenuResultTitleContainer{
  flex: 0 0 auto;
  width: 10vw;
  height: 14vw;
  min-width: 100px;
  min-height: 130px;
  margin: 1vw;
  position: relative;
}
.MenuResultTitleContainer img {
  flex: 0 0 auto;
  width: 10vw;
  height: 14vw;
  min-width: 100px;
  min-height: 130px;
  background: #101010;
}
.MenuResultTitleContainer div{
  position: absolute;
  bottom: 0;
  padding-left: .5vw;
  box-sizing: border-box;
  background: linear-gradient(transparent, #111111);
  width: 100%;
  border-bottom-right-radius: 1vw;
}
#SettingMenuCategory{
  display: none;
  position: fixed;
  top: 15%;
  right: 0;
  background: #0a0a0a;
  border: none;
  padding: 10px 18px;
  z-index: 5;
}
@media screen and (max-width:510px) {
  #TitleHeaderContainer{
    display: block;
  }
  #TitleHeaderContainer >div:first-child{
    text-align: center;
    overflow: hidden;
  }
  #TitleHeaderContainer>div:first-child img{
    width:50%;
    /* position: relative;
    bottom: -20px; */
  }
  #TitleHeader{
    box-sizing: border-box;
    height: 100%;
    padding: none;
    border-radius: 3vw;
    background: transparent;
  }
  #Dots {
    margin-top: 5px;
  }
  #TitleHeaderContainer>div:last-child{
    background-color: #131313;
    border-radius: 2vw;
    margin: 0;
    width: 100%;
    height: 180px;
    padding: 10px;
    box-sizing: border-box;
  }
  #RankingTitlesSliderGenresContainer{
    width: 100%;
  }
  #mainButtons{
    width: 100%;
    position: fixed;
    bottom:0;
    height: auto;
    left: 0;
    align-items: flex-end;
  }
  #mainButtons>button,#mainButtons>a{
    background-color: #111111;
    width: 50%;
    padding: 10px 0;
    border-radius: none;
    margin: 0;
    font-size: 1em;
    box-sizing: border-box;
    text-align: center;
  }
  #mainButtons>button{
    height: 41px;
  }
  #mainButtons > div{
    width: 50%;
    height: auto;
    position: absolute;
    background: #141414;
    overflow: none;
    bottom: 0;
    right: 0;
    flex-direction: column;
    border-radius: 20px;
  }
    #mainButtons>div button{
      padding: 10px 16px;
    }
  #mainText{
    width: 80vw;
  }
  #settingsForText{
    top: auto;
    bottom: 0;
    width: 100%;
    height: 48px;
  }
  #settingsForText>div{
    display: flex;
    width: 60vw;
    justify-content: space-around;
  }
  .SettingsWindow{
    top: auto;
    left: 50%;
    bottom: 47px;
    width: 80%;
    height: 80%;
    transform: translateX(-50%);
    background: #191919;
    z-index: 1;
  }
  #SettingChoose{
    height: 45%;
  }
}



@media screen and (max-width:700px) {
  .MainSliderChild img{
    width: 100px;
    height: 135px;
  }
  #MainSlider {
    font-size: .8em;
  }
  #HotTitles {
    display: block;
  }
  #HotTitlesTop{
    min-width: 100%;
  }
  #HotTitlesSlider{
    min-width: 100%;
    border-left: none;
    border-top: 1px solid rgb(105, 105, 105);
  }
  #HotTitlesHeader{
    font-size: 1.1em;
  }
  #ScoreSelect{
    width: 80%;
  }
  #ScoreSelect button{
    border-radius: 15px;
    margin: 6px 0;
    padding: 10px;
  }
  #menuCategory{
    transition: .7s;
    float: none;
    width: 90%;
    position: fixed;
    background: #101010;
    z-index: 6;
    right: -100%;
    padding: 10px;
    box-sizing: border-box;
  }
  #menuResult{
    width: 100%;
    justify-content:space-around;
  }
    .inputMenuText{
    width: 90%;
  }
  #SettingMenuCategory{
    display: block;
  }
}
@media screen and (max-width: 770px) {
  #TitleHeader .RankingTitlesSliderGenres{
    overflow-x: auto;
    min-height: 40px;
  }
  #HotTitlesSliderContainer{
    overflow-x: scroll;
    
  }
  #TitleBodyContainerSlider{
    overflow-x: auto;
  }
  #MainSlider {
    font-size: .85em;
  }
  
  .HotTitlesTopDescription {
    font-size: .85em;
  }
  .RankingTitlesChild{
    font-size: .9em;
  }
  .RankingTitlesInfo>div:first-child{
    overflow-x: auto;
    min-height: 40px;
  }
  #TitleBody{
  margin-top: 20px;
}
  .LastUpdatesChild{
    width: 100%;
  }
}


@media screen and (max-width: 1400px) {
  .HotTitlesTopDescription {
    font-size: 1.05em;
  }
}
@media screen and (max-width: 1200px) {
  .HotTitlesTopDescription {
    font-size: 1em;
  }
  .RankingTitlesChild{
    padding-right: 8px;
  }
  #TitleBodyHeader a{
  padding: 8px 14px;
  }
  #TitleBodyContainer{
    padding: 15px;
  }
  #Chapters > div{
    margin-top: 30px;
  }
  #comment {
    padding-right: 40px;
  }
  #HotTitlesHeader,#LastUpdates>h2,.RankingTitlesChild>h2 {
    border-radius: 15px 15px 0 0;
  }
  #content img{
    border-radius: 10px 0;
  }
  #LastUpdates,#RankingTitlesContainer{
    margin-top: 45px;
  }
  .RankingTitlesChild > div{
    padding: 13px;
    box-sizing: border-box;
  }
  .LastUpdatesChild{
    margin: 8px;
  }
  #Comments>div:last-child button{
    padding: 4px;
    margin-top: 10px;
  }
  .CommsAnswer{
    margin-top: 15px;
  }
  .MainSliderChild>a>div{
    padding: 10px;
  }
  #content{
    margin-top: 60px;
  }
  .SettingsWindow{
    right: 48px;
  }
  #SettChoose a{
    margin:12px 0 ;
    padding: 6px;
  }
  #Chapters a{
    padding: 5px;
    margin: 8px 0;
  }
  #SettFont > div{
    margin-top: 10px;
    padding: 8px;
  }
  .CommsAnswerChapter>div>p{
  margin: 10px 0;
  }
  #content .CommsAnswerChapter>img{
    margin-right: 10px;
  }
  .CommsAnswerChapter button{
    padding: 7px;
  }
  #CommentsChapter{
    padding: 0 25px 0 5px;
    border-radius: 15px;
  }
  .titleNext a{
    padding: 5px 16px;
  }
}
@media screen and (max-width: 992px) {
  .HotTitlesTopDescription {
    font-size: .98em;
  }
}

@media screen and (max-width: 576px) {
  .HotTitlesTopDescription{
    font-size: .8em;
  }
}


.pointEvent{
  pointer-events: none;
}





